html, body {
 margin: 0px;
 padding: 0px;
 border: 0px;
 }

 h1, h2, h3 {
  color: rgba(16, 16, 16, 0.87)
 }

svg {
	-webkit-animation: scale .6s;
	        animation: scale .6s;
}

@-webkit-keyframes scale {
	from { -webkit-transform: scale(.5); transform: scale(.5); }
	to { -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes scale {
	from { -webkit-transform: scale(.5); transform: scale(.5); }
	to { -webkit-transform: scale(1); transform: scale(1); }
}


